import React, { Component } from "react";
import Navbar from "./../components/Navbar";
import HeroSection from "./../components/HeroSection";
import PoolStats from "./../components/PoolStats";
import PoolLogs from "./../components/PoolLogs";
import TokensSection from "./../components/TokensSection";
import Balancers from "./../components/Balancers";
import Footer from "./../components/Footer";
import Button from "@material-ui/core/Button";
import { Link } from "./../util/router.js";



class Wrapper extends Component {
  constructor() {
    super();
    this.state = { activePoolId: null, pool: {name: 'none', tokens: []}, logs: [] };
  }

  setPool(pool) {
      this.setState({pool})
      this.setState({activePoolId: pool.id})
  }
  updatePool(pool) {
      this.setState({pool})
  }
  addLog(log) {
      this.state.logs.push(log)
      console.log(this.state.logs)
      this.setState({logs: this.state.logs})
  }

  render() {
    return (
      <div>
          <HeroSection
            bgColor="default"
            size="medium"
            bgImage=""
            bgImageOpacity={1}
            title="{ impSlap }"
            subtitle="Learn about Impermanent Loss / Divergence Loss. Simulate swaps and liquidity on Balancer pools and other AMM DEXes."
            buttonText={this.state.pool.name}
            buttonColor="primary"
            buttonPath="/"
          />
          <Balancers activePoolId={this.state.activePoolId} onChoose={(pool) => this.setPool(pool)} />
          <TokensSection
            bgColor="default"
            size="medium"
            bgImage=""
            bgImageOpacity={1}
            title="Swap & Add/Remove Liquidity"
            subtitle=""

            pool={this.state.pool}
            updatePool={(pool) => this.updatePool(pool)}
            items={[{name: 'renBTC'}, {name: 'WETH', description: 'Wrapped ETH'}]}
            addLog={(log) => this.addLog(log)}
          />
          <PoolLogs logs={this.state.logs} />
          <PoolStats
            pool={this.state.pool}
            bgColor="default"
            size="medium"
            bgImage=""
            bgImageOpacity={1}
           />
      </div>
    )
  }
}

/////////////////////////////////////////////


function IndexPage(props) {
  return (
    <>
      <Navbar
        color="default"
        logo="https://uploads.divjoy.com/logo.svg"
        logoInverted="https://uploads.divjoy.com/logo-white.svg"
      />
      <Wrapper />
      <Footer
        bgColor="default"
        size="medium"
        bgImage=""
        bgImageOpacity={1}
        description="Simulating swapping and liquidity in Balancer pools and beyond"
        copyright=" 2021 impSlap"
        logo=""
        logoInverted=""
        sticky={true}
      />
    </>
  );
}

export default IndexPage;
