import React from "react";
import Section from "./Section";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";

function _toFixedNum(str) {
    if (typeof str == 'undefined') { return '' }
    if (Math.abs(parseFloat(str)) < 1) {
      return parseFloat(str).toFixed(5)
    }
    return parseFloat(str).toLocaleString()
}
function _toPercent(str) {
    return (100*parseFloat(str)).toLocaleString() + '%'
}
function _toMegabucks(str) {
    if (typeof str == 'undefined') { return '' }
    let num = parseFloat(str)
    let megas = num/1000000
    return '$' + (megas < 1 ? megas.toFixed(2) : megas.toFixed(1)) + 'M'
}

// pool and totalWeight not strictly needed
function spotPrice(tokenIn, tokenOut, pool) {
  let Bo = tokenOut.balance
  let Bi = tokenIn.balance
  let Wi = tokenIn.denormWeight / pool.totalWeight
  let Wo = tokenOut.denormWeight / pool.totalWeight
  return (Bo / Wo) / (Bi / Wi)
}
function originalSpotPrice(tokenIn, tokenOut, pool) {
  let Bo = tokenOut.balanceOriginal
  let Bi = tokenIn.balanceOriginal
  let Wi = tokenIn.denormWeight / pool.totalWeight
  let Wo = tokenOut.denormWeight / pool.totalWeight
  return (Bo / Wo) / (Bi / Wi)
}
function netChange(cur, old) {
    return `${(cur - old > 0 ? '+' : '') + _toFixedNum(cur - old)} (${_toPercent((cur - old)/old)})`
}
function aParenB(a, b) {
    return `${a} (${b})`
}

function PoolStats(props) {
  const tok0 = props.pool.tokens[0]
  const tok1 = props.pool.tokens[1]
  const totalShares = parseFloat(props.pool.totalShares)

  let _valueIn = (bal0, bal1, price) => bal0/totalShares + price * bal1/totalShares
  let spot = 0
  let originalSpot = 0
  let hodlValueIn0 = 0
  let hodlValueIn1 = 0
  let valueIn0 = 0
  let valueIn1 = 0
  if (props.pool.tokens.length) {
      spot = spotPrice(tok0, tok1, props.pool)
      originalSpot = originalSpotPrice(tok0, tok1, props.pool)
      hodlValueIn0 = _valueIn(tok0.balanceOriginal, tok1.balanceOriginal, 1/spot)
      console.log(tok0.balanceOriginal, tok1.balanceOriginal, 1/spot, totalShares)
      hodlValueIn1 = _valueIn(tok1.balanceOriginal, tok0.balanceOriginal, spot)
      valueIn0 = _valueIn(tok0.balance, tok1.balance, 1/spot)
      valueIn1 = _valueIn(tok1.balance, tok0.balance, spot)
  }

  const items = props.pool.tokens.length == 0 ? [] : [
    {
      title: "Original Market Cap",
      stat: _toMegabucks(props.pool.liquidity)
    },
    {
      title: "Total BPT Shares",
      stat: _toFixedNum(totalShares)
    },
    {
      title: "Rate (Inverse)",
      stat: aParenB(tok0.symbol + '/' + tok1.symbol, tok1.symbol + '/' + tok0.symbol)
    },
    {
      title: "Original Spot Price (Inverse)",
      stat: aParenB(_toFixedNum(originalSpot), _toFixedNum(1/originalSpot))
    },
    {
      title: `Original 1 BPT had ${tok0.symbol} + ${tok1.symbol}` ,
      stat: _toFixedNum(tok0.balanceOriginal / totalShares) + ' + ' +  _toFixedNum(tok1.balanceOriginal / totalShares)
    },
    {
      title: `Current 1 BPT has ${tok0.symbol} + ${tok1.symbol}`,
      stat: _toFixedNum(tok0.balance / totalShares) + ' + ' + _toFixedNum(tok1.balance / totalShares)
    },
    {
      title: 'Total Pool ' + aParenB(tok0.symbol, tok1.symbol),
      stat: aParenB(_toFixedNum(tok0.balance), _toFixedNum(tok1.balance))
    },
    {
      title: "Current Spot Price (Inverse)",
      stat: aParenB(_toFixedNum(spot), _toFixedNum(1/spot))
    },
    {
      title: 'HODL Value of Original BPT in current spot ' + tok0.symbol + ` (${tok1.symbol})`,
      stat: aParenB(_toFixedNum(hodlValueIn0), _toFixedNum(hodlValueIn1))
    },
    {
      title: 'Value of 1 BPT in ' + tok0.symbol + ` (${tok1.symbol})`,
      stat: aParenB(_toFixedNum(valueIn0), _toFixedNum(valueIn1))
    },
    {
      title: 'Divergence Loss vs HODL in ' + tok0.symbol + ` (${tok1.symbol})`,
      stat: aParenB(_toFixedNum(valueIn0 - hodlValueIn0), _toFixedNum(valueIn1 - hodlValueIn1))
    },
    {
      title: '% Spot Price Change in ' + tok0.symbol + ` (${tok1.symbol})`,
      stat: aParenB(_toPercent(spot / originalSpot - 1), _toPercent((1/spot) / (1/originalSpot) - 1))
    },
    {
      title: 'Net Change in 1 BPT in ' + tok0.symbol,
      stat: netChange(tok0.balance / totalShares, tok0.balanceOriginal / totalShares)
    },
    {
      title: 'Net Change in 1 BPT in ' + tok1.symbol,
      stat: netChange(tok1.balance / totalShares, tok1.balanceOriginal / totalShares)
    },
    {
      title: '% Divergence Loss vs HODL in ' + tok0.symbol + ` (${tok1.symbol})`,
      stat: aParenB(_toPercent((valueIn0 - hodlValueIn0)/hodlValueIn0), _toPercent((valueIn1 - hodlValueIn1)/hodlValueIn1))
    },
    {
      title: "Swap Fee",
      stat: _toPercent(props.pool.swapFee)
    },
  ];

  return (
    <Section
      bgColor={props.bgColor}
      size={props.size}
      bgImage={props.bgImage}
      bgImageOpacity={props.bgImageOpacity}
    >
      <Container>
        <Grid container={true} justify="center" spacing={4}>
          {items.map((item, index) => (
            <Grid item={true} xs={12} sm={3} key={index}>
              <Box textAlign="center">
                <Typography variant="overline">{item.title}</Typography>
                <Typography variant="h4">{item.stat}</Typography>
              </Box>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Section>
  );
}

export default PoolStats;
