import React, { useState } from "react";
import Section from "./Section";
import Container from "@material-ui/core/Container";
import SectionHeader from "./SectionHeader";
import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Box from "@material-ui/core/Box";
import Avatar from "@material-ui/core/Avatar";
import Typography from "@material-ui/core/Typography";
import TextField from '@material-ui/core/TextField';
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  card: {
    display: "flex",
    flexDirection: "column",
    height: "100%",
  },
  avatarWrapper: {
    display: "flex",
    justifyContent: "center",
  },
  avatar: {
    width: theme.spacing(15),
    height: theme.spacing(15),
  },
}));

function _toFixedNum(str) {
    if (typeof str == 'undefined') { return '' }
    return parseFloat(str).toFixed(4)
}
function _tokenIcon(symbol) {
    return `https://icons.bitbot.tools/api/${symbol.toLowerCase()}/64x64`
}
function swap(tokenIn, tokenOut, pool, Ai) {
  //  Ao = Bo x (1 - (Bi / (Bi + Ai * (1-swapFee)))^(Wi/Wo))
  let Bo = tokenOut.balance
  let Bi = tokenIn.balance
  let Wi = tokenIn.denormWeight / pool.totalWeight
  let Wo = tokenOut.denormWeight / pool.totalWeight
  let swapFee = pool.swapFee

  let Ao = Bo * (1 - Math.pow(Bi / (Bi + Ai * (1 - swapFee)), Wi/Wo))
  tokenIn.balance += Ai // XXX need to setState
  tokenOut.balance -= Ao

  return Ao
}
function TokensSection(props) {
  const classes = useStyles();
  // XXX >2 tokens in pool...
  const [amount1, setAmount1] = useState(0)
  const [amount2, setAmount2] = useState(0)
  let setAmount = [setAmount1, setAmount2]
  let _setAmountIfNumeric = (amountStr, index) => isNaN(parseFloat(amountStr)) ? null : setAmount[index](parseFloat(amountStr))
  let amount = [amount1, amount2]
  const otherIndex = (index) => index == 0 ? 1 : 0
  const zwap = (index) => {
    let Aout = swap(props.pool.tokens[index], props.pool.tokens[otherIndex(index)], props.pool, amount[index])
    props.updatePool(props.pool)
    setAmount[otherIndex(index)](Aout)
    let log = `SWAP from ${amount[index]} ${props.pool.tokens[index].symbol} to ${Aout} ${props.pool.tokens[otherIndex(index)].symbol}`
    props.addLog(log)
  }
  // TODO calculate amount to add liquidity on both sides, keep price constant
  const addLiquidity = (index) => {
    props.pool.tokens[index].balance += amount[index]
    const addRemove = amount[index] > 0 ? 'ADD' : 'REMOVE'
    let log = `${addRemove} liquidity from ${props.pool.tokens[index].symbol}: ${amount[index]} `
    props.addLog(log)
  }

  return (
    <Section
      bgColor={props.bgColor}
      size={props.size}
      bgImage={props.bgImage}
      bgImageOpacity={props.bgImageOpacity}
    >
      <Container>
        <SectionHeader
          title={props.title}
          subtitle={props.subtitle}
          size={4}
          textAlign="center"
        />
        <Grid container={true} justify="center" spacing={4}>
          {props.pool.tokens.map((item, index) => (
            <Grid item={true} xs={12} sm={6} md={4} key={index}>
            <form noValidate autoComplete="off">
              <Card className={classes.card}>
                <CardContent>
                  <Box className={classes.avatarWrapper}>
                    <Avatar
                      src={_tokenIcon(item.symbol)}
                      alt={item.symbol}
                      className={classes.avatar}
                    />
                  </Box>
                  <Box textAlign="center" pt={3}>
                    <Typography variant="body2" component="p">
                      {item.symbol}
                    </Typography>
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      component="p"
                    >
                      {item.addess}
                    </Typography>
                    <Box mt={2}>
                      <TextField
                          id="outlined-helperText"
                          label="Amount (<0 Liquidity OK)"
                          value={amount[index]}
                          helperText={'Balance: ' + item.balance}
                          variant="outlined"
                          onChange={event => _setAmountIfNumeric(event.target.value, index)}
                      />
                      <Typography variant="body1" component="p">
                        Token weight: {_toFixedNum(100 * item.denormWeight / props.pool.totalWeight)}%
                      </Typography>
                      <Button
                        variant="contained"
                        size="large"
                        color={"primary"}
                        onClick={() => zwap(index)}
                      >
                        Swap In
                      </Button>
                      <Button
                        variant="contained"
                        size="large"
                        color={"primary"}
                        onClick={() => addLiquidity(index)}
                      >
                        Add Liquidity
                      </Button>
                    </Box>
                  </Box>
                </CardContent>
              </Card>
            </form>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Section>
  );
}

export default TokensSection;
