import React, { useState, Component } from "react";
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import InboxIcon from '@material-ui/icons/Inbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import { makeStyles } from "@material-ui/core/styles";

const axios = require('axios')

function shortHex(hex) {
    return hex.slice(0, 6) + '...' + hex.slice(-4)
}
function poolName(pool) {
    const sumWeight = pool.totalWeight
    return pool.tokens.map(tok => `${(100 * tok.denormWeight / sumWeight).toFixed(2)}% ${tok.symbol}`).join(' ')
}
function addPoolName(pools) {
    return pools.map(pool => { return { name: poolName(pool), ...pool } })
}
async function getBalancer(addr) {
    try {
        let res = await axios.post('https://api.thegraph.com/subgraphs/name/balancer-labs/balancer', {
          query: `
            {
              pools(first: 10, orderBy: liquidity, orderDirection: desc, where: {publicSwap: true, finalized: true, active: true}) {
                id
                swapFee
                totalShares
                totalWeight
                liquidity
                tokensList
                tokens {
                  id
                  symbol
                  address
                  balance
                  denormWeight
                }
              }
            }
          `
        })

        console.log(res.data.data.pools)
        let retval = res.data.data.pools
        retval = addPoolName(retval)
        for (let poolIndex = 0; poolIndex < retval.length; poolIndex++) {
          for (let tokenIndex = 0; tokenIndex < retval[poolIndex].tokens.length; tokenIndex++) {
            retval[poolIndex].tokens[tokenIndex].balance = parseFloat(retval[poolIndex].tokens[tokenIndex].balance)
            retval[poolIndex].tokens[tokenIndex].balanceOriginal = retval[poolIndex].tokens[tokenIndex].balance
            retval[poolIndex].tokens[tokenIndex].denormWeight = parseFloat(retval[poolIndex].tokens[tokenIndex].denormWeight)
          }
          retval[poolIndex].swapFee = parseFloat(retval[poolIndex].swapFee)
          retval[poolIndex].totalWeight = parseFloat(retval[poolIndex].totalWeight)
        }

        return retval
    } catch(error) {
      console.error(error)
      return null
    }
}

class Balancers extends Component {
  constructor() {
    super();
    this.state = { pools: [] };
  }

  componentDidMount() {
    getBalancer().then(retval => this.setState({pools: retval}))
  }

  render() {
    return (
      <Grid container={true} justify="center" spacing={4}>
        <Accordion defaultExpanded={true} >
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>Top 10 Balancer Pools (CLICK ONE)</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <List>
              {this.state.pools.map(pool => (
                <ListItem key={pool.id}>
                  <ListItemText>{shortHex(pool.id)}</ListItemText>
                  <Button
                    variant={pool.id == this.props.activePoolId ? "contained" : "outlined"}
                    size="large"
                    color={"primary"}
                    onClick={() => this.props.onChoose(pool)}
                  >
                    {pool.name}
                  </Button>

                </ListItem>
              ))}
            </List>
          </AccordionDetails>
        </Accordion>
      </Grid>
    );
  }
}
export default Balancers;
